export default {
	data() {
		return {
			title: "",
			userList: ""
		}
	},
	onLoad(object) {
		this.userList = JSON.parse(object.userList)
		this.title = object.title
	},
	methods: {
		tohomepage(userId){
			uni.navigateTo({
				url: '/pages/subscriber/homepage?userId='+userId
			});	
		}
	}
}
